.form{
    display: flex;
    flex-direction: column;
    padding: 12px;
    width: 400px;
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    margin-bottom: 16px;
}

.label{
    display: inline-block;
    margin-bottom: 16px;
    font-size: 20px;
    color: #171718;
    cursor: pointer;
}

.input{
    color: #171718;
    font: inherit;
    font-size: 1.2rem;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #bdbdbd;
    width: 350px;
    outline: 0;
}

.button{
    display: inline-flex;
    justify-content: center;
    color: #fff;
    background-color: #077c4b;
    border: 0;
    padding: 8px 24px;
    border-radius: 4px;
    cursor: pointer;
    text-transform: uppercase;
    user-select: none;
    transition: background-color 200ms ease-in-out;
  
}